import React, { useState,useEffect} from 'react'
import './home.css'
import Lunbo from './Lunbo';
import { connect } from 'react-redux';
import { list } from './actionCreators';
import ReactDOM from 'react-dom';
// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>

const Home = (props) => {
   console.log(props);
   const ex = () => {
      props.dispatch(list());
   };
   ex();
   return (
      <div>
         <div>
            <header id="header" >
               <div id="search">
                  <a id="search2" onClick={() => props.history.push('/home/search')}>
                     <span className="sp iconfont icon-sousuo">寻找宝贝店铺</span>
                  </a>
               </div>
            </header>
            <div id="lunbo">
               <Lunbo />
            </div>
            <div id="div-0">
               <div>
                  <div id="a">
                     {
                        props.datalist.map((item) => {
                           return <div id="div-1">
                              <a id="a-1">
                                 <img id="img" src={item.img} alt="" />
                                 <a id="a-2">
                                    <p id="money">￥{item.price}</p>
                                    <p id="m">{item.title}</p>
                                 </a></a>
                           </div>
                        })
                     } 
                  </div>
               </div>
            </div>
         </div>

      </div >
   )
}
const mapStateToProps = (state) => ({
   datalist: state. datalist
})
export default connect(mapStateToProps)(Home);
