import React, { useState, useEffect } from 'react';
import { HashRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom';
import { Button, TabBar } from 'antd-mobile';
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import Cart from './taobao/Cart';
import Order from './taobao/Order';
import My from './taobao/My';
import Search from './taobao/Search';
import Linkcommodity from './taobao/Linkcommodity';
import { Provider } from 'react-redux';
import store from './taobao/Store';
import Todos from './taobao/Todos';
import Homelogin from './taobao/Login';
const App = () => {
	return <Provider store={store}>
		<Router >
			<Switch>
				<Route exact path='/' component={Home} />
				<Route path='/cart' component={Cart} />
				<Route path='/order' component={Order} />
				<Route path='/my' component={My} />
				<Route path='/home/login' component={Homelogin} />
				<Route path='/home/linkcommodity' component={Linkcommodity} />
				<Route path='/home/search' component={Search} />
			</Switch>
			<Mytab />
		</Router>
	</Provider>
}

export default App;