import React, { useState } from 'react';
import { connect } from 'react-redux';
import { login } from './actionCreators';
const Homelogin = (props) => {
    const log = () => {
        props.dispatch(login())
    }
    console.log(props.username);
    return (
        <div>
            {
                props.username
                    ? <div style={{ width:'154px',height:'100px',margin: '100px auto' }}>
                        <span style={{ border: '0', float:'left',width:'150px',height:'20px', display: 'block',background: '#ddd',textAlign:'center' }}>已登录成功</span>
                        <button
                            onClick={props.history.goBack} style={{ height: '30px', width: '150px', display: 'block',background: '#ff5000', border: '0' }}
                        >返回</button>
                    </div>
                    :  <div  style= {{width:'154px',height:'100px',margin: '100px auto' }}> 
                        <input style= {{  border: '0', float:'left',width:'150px',height:'20px', display: 'block',background: '#ddd',textAlign:'center'}} name="" type="text" value="张三" ></input>
                        <input style= {{  border: '0' ,float:'left',width:'150px',height:'20px', display: 'block',background: '#ddd',textAlign:'center'}}name="" type="password" value="张三1234" ></input>
                        <button
                        onClick={log} style={{ height: '30px', width: '150px', display: 'block',background: '#ff5000', border: '0' }}
                    >登录</button>
                    </div>
            }
        </div>
    )
}

const mapStateToProps = (state) => ({
    username: state.userinfo
})
export default connect(mapStateToProps)(Homelogin);