import { SearchBar, Button, WhiteSpace, WingBlank } from 'antd-mobile';
import React from 'react';
const Search = (props) => {
  return (
      <div>
       <SearchBar
        placeholder="Search"
        onCancel={() => props.history.push('/')}
        showCancelButton
      />
      </div>
  )
}

export default Search;