import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import { login } from './actionCreators';
class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        console.log(pathname.split('/'));
        if (pathname.split('/').length > 2) {
            return null;
        }
        return (
            <div style={{
                position: 'fixed',
                width: '100%',
                bottom: 0
            }}>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-shouye1'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouye'></i>
                        }
                        title="首页"
                        key="home"
                        selected={pathname === '/'}
                        onPress={() => {
                            this.props.history.push('/');
                        }}
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-shoucang'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang1'></i>

                        }
                        title="收藏"
                        key="cart"
                        selected={pathname === '/cart'}
                        onPress={() => {
                            console.log(this.props);
                            {
                               /* if (this.props.userinfo=='') {*/
                                    this.props.history.push('/home/login')
                               /* } else {
                                    this.props.history.push('/cart')
                                }*/
                            }
                        }}
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdanliebiao'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdanliebiao1'></i>
                        }
                        title="订单列表"
                        key="order"
                        selected={pathname === '/order'}
                        onPress={() => {
                            this.props.history.push('/order')
                        }}
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-My'></i>}
                        selectedIcon={
                            <i className='iconfont icon-wodedangxuan'></i>
                        }
                        title="我的淘宝"
                        key="my"
                        selected={pathname === '/my'}
                        onPress={() => {
                            this.props.history.push('/my')
                        }}
                    >
                    </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);
