const reducer = (state = store, action) => {
    switch (action.type) {
        case 'LOGIN':
            return { username: action.username }
        case 'LIST':
            return Object.assign({}, state, { datalist: [...state.datalist, action.listdata] })
        case 'COLLECTION':
            return Object.assign({}, state, { shoucang: [...state.shoucang, action.shoucang] })
        case 'ADD_TODO':
            return Object.assign({}, state, { shoucang: [...state.shoucang, action.shoucang] })
        case 'DEL_TODO':
            return Object.assign({}, state, { shoucang: [...state.shoucang, action.shoucang] })
        default:
            return state;
    }
}
export default reducer;